@baseFont : 100 / 750 *1vw;
.app-login {
    background: #fff;
    height: 100vh;
    text-align: center;
}
.app-login-body {
    padding-top: 48*@baseFont;
    text-align: left;
    margin: 0 36*@baseFont;
    h1 {
        font-family: PingFang-SC-Bold;
        font-weight: bold;
        color: rgba(51, 51, 51, 1);
        font-size: 50*@baseFont;
    }
    .form {
        display: flex;
        height: 80*@baseFont;
        line-height: 80*@baseFont;
        margin: 40*@baseFont auto;
        margin-top: 50*@baseFont;
        .form-input {
            flex: 1;
            position: relative;
            input {
                font-size: 34*@baseFont;
                text-indent: 10*@baseFont;
                background: transparent;
                border: 0 none;
                border-bottom: azure 1*@baseFont solid;
                resize: none;
                outline: none;
                /*清除选中效果的默认蓝色边框 */
                -webkit-appearance: none;
                -moz-appearance:textfield;
                /*清除浏览器默认的样式 */
                line-height: 40*@baseFont  !important;
            }
            #phone{
                font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif
            }
            input::-webkit-input-placeholder {
                /* WebKit browsers */
                line-height: 40*@baseFont  !important;
                color: #efefef;
                text-indent: 10*@baseFont;
                font-weight: 300;
            }

            input:focus,
            input:active {
                color: rgb(4, 179, 77);
                text-indent: 10*@baseFont;
                background: #fff;
            }

            .form-input-btn {
                display: inline-block;
                width: 112*@baseFont  !important;
                font-size: 16*@baseFont
            }

        }
    }

    .form-submit {
        display: block;
        background: rgb(4, 179, 77);
        border: none;
        border-radius: 10*@baseFont;
        margin: 20*@baseFont auto;
        margin-top: 80*@baseFont  !important;
        width: 80%;
        font-size: 40*@baseFont;
        color: #fff;
        height: 80*@baseFont;
        line-height: 80*@baseFont;
    }

    .form-submit:active {
        background: rgb(67, 226, 133);
        box-shadow: 0*@baseFont 0*@baseFont 5*@baseFont #888888;
    }

    .login-footer {
        //这里的定位为了阻止键盘弹起破坏布局
        position: absolute;
        width: calc(100vw - 72*@baseFont);
        top: 900*@baseFont;
        .footer-hr {
            width: 100%;
            height: 40*@baseFont;
            line-height: 40*@baseFont;
            font-size: 24*@baseFont;
            text-align: center;
            .line {
                display: inline-block;
                width: 100px;
                border-top: 1px solid #cccccc;
                vertical-align: 5px;
            }
        }
        .login-footer-content {
            display: flex;
            justify-content: center;
        }

        .login-weixin {
            text-align: center;
            color: rgb(148, 148, 148);
            font-size: 24*@baseFont;

            .icon-weixin {
                color: rgb(2, 180, 76);
                font-size: 60*@baseFont;
            }
        }
    }



}