@baseFont : 100 / 750 *1vw;

.c-toast{
    //遮罩层
    .c-toast-p{
        position: fixed;
        left:0; 
        right:0; 
        top:0; 
        bottom:0; 
        width:100vw;
        height:100vh;
    }
    .c-toast-success{
        position: fixed;
        width:200*@baseFont;
        height: 200*@baseFont;
        border-radius: 20*@baseFont;
        margin:auto; 
        left:0; 
        right:0; 
        top:0; 
        bottom:0; 
        z-index: 9999;
        background: rgb(87, 87, 87);
        opacity: 0.8;
        text-align:center;
        box-sizing: border-box;
        padding: 50*@baseFont;
    }
    .c-toast-error{
        position: fixed;
        width:200*@baseFont;
        height: 200*@baseFont;
        border-radius: 20*@baseFont;
        margin:auto; 
        left:0; 
        right:0; 
        top:0; 
        bottom:0; 
        z-index: 9999;
        background: rgb(87, 87, 87);
        opacity: 0.8;
        text-align:center;
        box-sizing: border-box;
        padding: 50*@baseFont;
    }
    .c-toast-loading{
        position: fixed;
        width:200*@baseFont;
        height: 200*@baseFont;
        border-radius: 20*@baseFont;
        margin:auto; 
        left:0; 
        right:0; 
        top:0; 
        bottom:0; 
        z-index: 9999;
        background: rgb(87, 87, 87);
        opacity: 0.8;
        text-align:center;
        box-sizing: border-box;
        padding: 50*@baseFont;
    }
    .c-toast-loading-font{
        position: relative;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width:98*@baseFont;
        height: 98*@baseFont;
        line-height: 98*@baseFont;
        overflow: hidden;
        font-size: 100*@baseFont;
        text-align: center;
        display:block;
        margin: 0 auto;
        padding: 0;
        color: #fff;
        border-radius: 50*@baseFont;
        animation:mymove 1.2s linear infinite;
    }
    //旋转动画
    @keyframes mymove{
        from {
          transform: rotate(0deg);
        }
        to {
          transform: rotate(360deg);
        }
      
      
    }
    .c-toast-loading-success{
        position: relative;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width:98*@baseFont;
        height: 98*@baseFont;
        line-height: 98*@baseFont;
        overflow: hidden;
        font-size: 100*@baseFont;
        text-align: center;
        display:block;
        margin: 0 auto;
        padding: 0;
        color: rgb(8, 245, 39);
        border-radius: 50*@baseFont;
    }
    .c-toast-loading-error{
        position: relative;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width:98*@baseFont;
        height: 98*@baseFont;
        line-height: 98*@baseFont;
        overflow: hidden;
        font-size: 100*@baseFont;
        text-align: center;
        display:block;
        margin: 0 auto;
        padding: 0;
        color: rgb(8, 245, 39);
        border-radius: 50*@baseFont;
    }
}

