.c-toast .c-toast-p {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
}
.c-toast .c-toast-success {
  position: fixed;
  width: 26.66666667vw;
  height: 26.66666667vw;
  border-radius: 2.66666667vw;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999;
  background: #575757;
  opacity: 0.8;
  text-align: center;
  box-sizing: border-box;
  padding: 6.66666667vw;
}
.c-toast .c-toast-error {
  position: fixed;
  width: 26.66666667vw;
  height: 26.66666667vw;
  border-radius: 2.66666667vw;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999;
  background: #575757;
  opacity: 0.8;
  text-align: center;
  box-sizing: border-box;
  padding: 6.66666667vw;
}
.c-toast .c-toast-loading {
  position: fixed;
  width: 26.66666667vw;
  height: 26.66666667vw;
  border-radius: 2.66666667vw;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999;
  background: #575757;
  opacity: 0.8;
  text-align: center;
  box-sizing: border-box;
  padding: 6.66666667vw;
}
.c-toast .c-toast-loading-font {
  position: relative;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 13.06666667vw;
  height: 13.06666667vw;
  line-height: 13.06666667vw;
  overflow: hidden;
  font-size: 13.33333333vw;
  text-align: center;
  display: block;
  margin: 0 auto;
  padding: 0;
  color: #fff;
  border-radius: 6.66666667vw;
  animation: mymove 1.2s linear infinite;
}
@keyframes mymove {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
