@baseFont : 100 / 350 * 1vw;

.c-header {
    height: 50*@baseFont;
    line-height: 50*@baseFont;
    position: relative;
}

.c-header-goBack {
    font-size: 24*@baseFont;
    position: absolute;
    left: 15*@baseFont;
    color: rgb(160, 160, 160);
}

.c-header-title {
    font-size: 14*@baseFont;
    font-family: PingFang-SC-Medium;
    font-weight: 500;
    color: rgb(112, 112, 112);
}